<template>
	<view>
		<cu-custom bgColor="bg-gradual-blue" :isBack="true">
			<block slot="content">新建、修改座位</block>
		</cu-custom>
		<!-- <homecom class="homclass"></homecom> -->
		<view class="formcontent">
			<form>
				<view class="cu-form-group margin-top">
					<view class="title">选择楼层</view>
					<picker @change="PickerChange" :value="index" :range="picker">
						<view class="picker">
							{{index>-1?picker[index]:'请选择'}}
						</view>
					</picker>
				</view>
				<view class="cu-form-group">
					<view class="title"><text class="requireclass">*</text>座位名称</view>
					<input placeholder="请输入座位名称" v-model="form.seatdesc"></input>
				</view>
				<view class="cu-form-group">
					<view class="title"><text class="requireclass">*</text>详细地址</view>
					<input placeholder="请输入详细地址" v-model="form.address"></input>
				</view>
				<view class="cu-form-group">
					<view class="cu-form-group">
						<view class="title">是否启用</view>
						<switch @change="SwitchA" :class="switchA?'checked':''" :checked="switchA?true:false"></switch>
					</view>
				</view>
			</form>
			<view class="margin-top submitbut">
				<button class="cu-btn block bg-blue margin-tb-sm lg" @click="submit">提交</button>
			</view>
		</view>
	</view>
</template>

<script>
	import request from '../../api/api.js'
	import urls from '../../api/request.js'
	import {
		mobileNoTest
	} from '../../utils/rules.js'
	import {
		deepCopy
	} from '../../utils/expressfun.js'
	import homecom from "@/components/homecom/homecom.vue"
	export default {
		components: {
			homecom
		},
		onLoad(option) {
			this.id = option.id
			if (this.id != 'add') {
				this.form.seatdesc = option.seatdesc
				this.form.address = option.address
				this.index = option.floor
				this.switchA =  option.status == '正常' ? true : false
			}

		},
		mounted() {
			this.init()
		},
		data() {
			return {
				switchA: false,
				id: '',
				form: {
					seatdesc: '',
					address: '',
					floor:'',
				},
				index: -1,
				picker: [],

			}
		},
		methods: {
			init() {
				this.queryfloor()
			},
			SwitchA(e) {
				this.switchA = e.detail.value
			},
			async queryfloor() {
				let queryobj = {}
				let resdata = await new this.Request(this.Urls.m().floorquery, queryobj).modepost()
				console.log(resdata)
				resdata.forEach((val, i) => {
					this.picker.push(val.floordesc)
				})
			},
			async submit() {
				if (this.id == 'add') {
					let obj = deepCopy(this.form)
					obj.floor = this.index
					obj.status = this.switchA? '正常' : '停用'
					let resdata = await new this.Request(this.Urls.m().seatinsert, obj).modepost()
					if (resdata == 'success') {
						this.alertobj.diagfun('操作成功')
						setTimeout(() => {
							uni.navigateTo({
								url: `../setseatlist/setseatlist`
							});
						}, 1500)
					}
				} else {
					let obj = deepCopy(this.form)
					obj.id = this.id
					obj.floor = this.index
					obj.status = this.switchA? '正常' : '停用'
					let resdata = await new this.Request(this.Urls.m().seatedit, obj).modepost()
					if (resdata == 'success') {
						this.alertobj.diagfun('操作成功')
						setTimeout(() => {
							uni.navigateTo({
								url: `../setseatlist/setseatlist`
							});
						}, 1500)
					}
				}
			},
			PickerChange(e) {
				this.index = e.detail.value
			},


			validfun() {
				let flag = mobileNoTest(this.form.mobile)
				if (flag) {
					uni.showToast({
						title: '手机号验证通过',
						icon: 'none',
						duration: 1000
					});
				} else {
					uni.showToast({
						title: '手机号验证不通过',
						icon: 'none',
						duration: 1000
					});
					this.form.mobile = ""
				}
			},
		}
	}
</script>

<style scoped>
	.submitbut {
		padding: 15px;
	}

	.titlesub {
		margin-right: 10px;
	}
</style>
